@mixin base {
    @include containerPadding();
    
    background-color: $component-content-inverse-background;
    color: $component-content-inverse-text;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    @media (min-width: 801px) {
    }
}

.page-footer {
    @include base;

    @import "branding";
    @import "content";
    @import "copyright";
    @import "navigation";
}